import { _decorator, Component, Node } from 'cc';
import { SubGameMgr } from '../../module_basic/scripts/SubGameMgr';
import { GameMgr } from '../../module_basic/scripts/GameMgr';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';
import { ModuleDef } from '../../scripts/ModuleDef';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
const { ccclass, property } = _decorator;

@ccclass('BingoFrogGameMgr')
export class BingoFrogGameMgr extends GameMgr {
     private static _inst: BingoFrogGameMgr;
        public static get inst(): BingoFrogGameMgr {
            if (!this._inst) {
                this._inst = new BingoFrogGameMgr();
            }
            return this._inst;
        }
    
        constructor() {
            super();
            this._gameType = SubGameDef.BINGO_FROG;
            this._entryScene = { name: 'lobby_bingo_frog', bundle: ModuleDef.BINGO_FROG };
            this._gameScene = { name: 'game_bingo_frog', bundle: ModuleDef.BINGO_FROG };
            this.initNetMsgHandlers();
        }
        initNetMsgHandlers() {
    
        }
    
        initSoloMode() {
            RoomMgr.inst.reset();
            this.reset();
            SubGameMgr.gameMgr = this;
        }
}
// console.error("BingoFrogGameMgr 加载成功");
SubGameMgr.inst.registerGameMgr(BingoFrogGameMgr.inst);


