import { Enum } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { BingoFrogCardManager } from './BingoFrogCardManager';
import { macro } from 'cc';
import { director } from 'cc';
import { Prefab } from 'cc';
import { NodePool } from 'cc';
import { instantiate } from 'cc';
import { find } from 'cc';
import { Sprite } from 'cc';
import { Vec3 } from 'cc';
import { sp } from 'cc';
import { Label } from 'cc';
import { UIOpacity } from 'cc';
import { v3 } from 'cc';
import { tween } from 'cc';
import { BingoFrogData } from './BingoFrogData';
import { randomRangeInt } from 'cc';
const { ccclass, property } = _decorator;

const testJson = {
    cards: [{
        items: [{ number: 5 }, { number: 12 }, { number: 3 }, { number: 8 }, { number: 56 }, { number: 45 }, { number: 50 }, { number: 60 }, { number: 42 }, { number: 22 }, { number: 23 }, { number: 7 }, { number: 10 },
        { number: 15 }, { number: 18 }, { number: 32 }, { number: 34 }, { number: 65 }, { number: 70 }, { number: 1 }, { number: 27 }, { number: 66 }, { number: 13 }, { number: 59 }, { number: 6 }
        ]
    }],
    items: [[]],
    //===============================
    sendBalls: [6, 12, 70, 60, 50, 51, 8, 6, 7, 8, 51, 10, 23, 24, 36, 27, 66, 64, 13, 58, 59, 17, 15, 11, 12, 34, 6, 8, 66, 46, 56, 1, 55, 34, 71, 46, 44, 43, 42, 40, 8, 7]//42球
}
const bingoLab = ['B', 'I', 'N', 'G', 'O']; //发出的球中的字母
const dripWays = [new Vec3(-90, 53), new Vec3(14, 42), new Vec3(96, -1), new Vec3(143, -63), new Vec3(138, -124)];//球的每一颗路劲

@ccclass('BingoFrogMain')
export class BingoFrogMain extends Component {
    @property(BingoFrogCardManager) cardMgr: BingoFrogCardManager = null;
    @property(Prefab) ballPre: Prefab = null;
    @property({ type: Prefab, tooltip: '卡牌预制体' }) cardPre: Prefab = null;
    @property({ type: [Node], tooltip: '卡牌容器' }) cardContent: Node[] = [];
    @property(Node) ballContent: Node = null;
    @property(Node) bingoRuleContent: Node = null;
    @property(Prefab) ruleBlock: Prefab = null;
    @property({ type: Label, tooltip: '已发出的球数量' }) ballNumLab: Label = null;
    @property({ type: Label, tooltip: '提示该局发出的球数' }) totalBallLab: Label = null;
    @property({ type: Node, tooltip: 'bingo信息节点' }) bingoInfoNode: Node = null;
    @property({ type: Node, tooltip: '奖池bingo信息节点' }) turboInfoNode: Node = null;
    @property({ type: Node, tooltip: 'bingo信息位置节点' }) bingoInfoPosNode: Node = null;
    @property({ type: Node, tooltip: '奖池bingo信息位置节点' }) turboInfoPosNode: Node = null;
    @property({ type: Node, tooltip: '单张牌节点' }) oneCardNode: Node = null;
    @property({ type: Node, tooltip: '多张牌节点' }) moreCardNode: Node = null;

    private _ballPool: NodePool = new NodePool();
    private _bingoRuleIndex: number = 0;
    private _ballArr: number[] = [];

    //---------------------------------
    private _sendBall: number[] = [];

    protected start(): void {
        this.createrSendBall();
        this.initGame();
    }

    private initGame() {
        if (this.bingoRuleContent.children.length <= 0) {
            for (let i = 0; i < 5; i++) {
                const item = instantiate(this.ruleBlock);
                item.parent = this.bingoRuleContent;
            }
        }
        this.schedule(this.showBingoRule, 2, macro.REPEAT_FOREVER, .1); //开启中奖路线轮播
        this.ballNumLab.node.parent.active = false;
        director.on(BingoFrogData.EventName.buyCardSuccess, this.buyCardSuccess, this);
        //---------------------------------------------------
        // this.cardMgr.initCard(testJson.cards[0]);
        // this.schedule(this.testSendBall, 3, macro.REPEAT_FOREVER, 3);
        this.totalBallLab.string = this._sendBall.length + " BALLS";
        director.on(BingoFrogData.EventName.bingo, this.bingo, this);
    }

    private buyCardSuccess(cardNumer) {
        if (cardNumer === 1) {
            this.oneCardNode.active = true;
            this.oneCardNode.scale = Vec3.ZERO;
            this.oneCardNode.getComponent(UIOpacity).opacity = 0;
            tween(this.oneCardNode).to(.2, { scale: Vec3.ONE }).start();
            tween(this.oneCardNode.getComponent(UIOpacity)).to(.2, { opacity: 255 }).start();

            //---------------------------------------------------
            this.cardMgr.initCard(this.createrTestData(), 0);
        } else {
            this.moreCardNode.active = true;
            for (let i = 0; i < this.cardContent.length; i++) {
                if (i < cardNumer) {
                    const card = instantiate(this.cardPre);
                    card.parent = this.cardContent[i];
                    card.getComponent(BingoFrogCardManager).initCard(this.createrTestData(), i);
                }

                this.cardContent[i].scale = Vec3.ZERO;
                this.cardContent[i].getComponent(UIOpacity).opacity = 0;
                this.scheduleOnce(() => {
                    tween(this.cardContent[i]).to(.2, { scale: Vec3.ONE }).start();
                    tween(this.cardContent[i].getComponent(UIOpacity)).to(.2, { opacity: 255 }).start();
                }, .2 * i);
            }
        }

        //---------------------------------------------------
        this.schedule(this.testSendBall, 3, macro.REPEAT_FOREVER, 3);
    }

    //-------测试模拟发球--------
    private _sendIndex = 0;
    private testSendBall() {
        this.addCommonBall(this._sendBall[this._sendIndex]);
        this._ballArr.push(this._sendBall[this._sendIndex]);
        this.ballNumLab.node.parent.active = true;
        this.ballNumLab.string = this._ballArr.length + '';
        director.emit(BingoFrogData.EventName.newBall, this._sendBall[this._sendIndex]);
        this._sendIndex++;
        if (this._sendIndex >= this._sendBall.length) {
            this.unschedule(this.testSendBall);
            this._sendIndex = 0;
        }
    }

    private createrTestData() {
        const numbers = BingoFrogData.getRandomNumbersInRange(1, 75, 25);
        const data = { items: [] };
        for (let i = 0; i < numbers.length; i++) {
            const item = { number: numbers[i] };
            data.items.push(item);
        }

        return data;
    }

    private createrSendBall() {
        const num = randomRangeInt(45, 73);
        this._sendBall = [];
        for (let i = 0; i < num; i++) {
            const item = BingoFrogData.getRandomNumbersInRange(1, 75, 1);
            this._sendBall.push(item[0]);
        }
    }
    //----------------------------------

    private bingo() {
        //bingo信息移出屏幕外的终点位置
        const endPos = this.bingoInfoPosNode.position.clone().add3f(1000, 0, 0);
        //奖池信息移出屏幕外的终点位置
        const endTurboPos = this.turboInfoPosNode.position.clone().add3f(1000, 0, 0);
        tween(this.bingoInfoNode).to(2, { position: endPos })
            .delay(.1)
            .call(() => {
                this.turboInfoNode.setWorldPosition(this.bingoInfoNode.worldPosition.clone());
            })
            .delay(1)
            .call(() => {
                //奖池信息移动至bingo信息未移动的位置
                tween(this.turboInfoNode).to(2, { worldPosition: this.bingoInfoPosNode.worldPosition.clone() }).call(() => {
                    director.emit(BingoFrogData.EventName.Jackpot);
                }).start();
            }).start();

        tween(this.turboInfoNode).to(2, { position: endTurboPos }).start();

        //--------------------------------------------------------
        this.unschedule(this.testSendBall);
        this.schedule(this.testSendBall, 1, macro.REPEAT_FOREVER, 6);
    }

    //展示bingo的中奖路线
    private showBingoRule() {
        const data = BingoFrogData.bingoWayRules[this._bingoRuleIndex];
        for (let i = 0; i < data.length; i++) {
            const carryY = data[i] % 5;
            const carryX = Math.floor(data[i] / 5);
            const pos = v3(-39 + 20 * carryX, 39 - 20 * carryY);
            this.bingoRuleContent.children[i].position = pos;
        }

        this._bingoRuleIndex++;
        if (this._bingoRuleIndex > BingoFrogData.bingoWayRules.length - 1) this._bingoRuleIndex = 0;
    }

    private addCommonBall(ballNumer: number) {
        let ball: Node = null;
        if (this._ballPool.size() > 0) {
            ball = this._ballPool.get();
        } else {
            ball = instantiate(this.ballPre);
        }
        ball.parent = this.ballContent;
        this.hideDrip(ball);
        find('label', ball).getComponent(Label).string = ballNumer.toString();
        //取BINGO中的一个字母，每15个数字一个字母
        find('bingo', ball).getComponent(Label).string = bingoLab[Math.floor((ballNumer - 1) / 15)];
        ball.scale = Vec3.ZERO;
        ball.getComponent(UIOpacity).opacity = 0;
        ball.position = v3(-90, 53);
        tween(ball.getComponent(UIOpacity)).to(0.5, { opacity: 255 }).start();
        tween(ball).to(0.5, { scale: new Vec3(1, 1, 1) }).start();
        this.moveCommonBall();
    }

    moveCommonBall() {
        if (this.ballContent.children.length > 1 && this.ballContent.children.length <= 5) {
            let scale = new Vec3();
            for (let index = 0; index < this.ballContent.children.length - 1; index++) {
                const element = this.ballContent.children[index];
                const pos = dripWays[this.ballContent.children.length - 1 - index];
                const measure = 1 - 0.2 * (this.ballContent.children.length - 1 - index);
                scale = new Vec3(measure, measure, measure);
                tween(element).to(0.5, { scale: scale, position: pos }).call((ball: Node) => {
                    if (this.ballContent.children.length === 5) {
                        if (index === 0) {
                            this.showDrip(ball);
                        }
                    }
                }).start();
            }
        }
        // if (this.ballContent.children.length >= 7) {
        //     for (let i = 6; i < this.ballContent.children.length; i++) {
        //         this.ballContent.children[this.ballContent.children.length - 1].removeFromParent();
        //         if (this.ballContent.children.length <= 6) break;
        //     }
        // }
        if (this.ballContent.children.length < 6) return;
        const ball = this.ballContent.children[0];
        tween(ball).to(0.5, { position: new Vec3(138, -530) }).call((node: Node) => {
            this._ballPool.put(node);
        }).start();
        let scale = new Vec3();
        for (let i = 1; i < this.ballContent.children.length - 1; i++) {
            const ball2 = this.ballContent.children[i];
            const pos = dripWays[this.ballContent.children.length - 1 - i];
            const measure = 1 - 0.2 * (this.ballContent.children.length - 1 - i);
            scale = new Vec3(measure, measure, measure);
            tween(ball2).to(0.5, { scale: scale, position: pos }).call((ball: Node) => {
                if (i === 1) {
                    this.showDrip(ball);
                }
            }).start();
        }
    }

    private showDrip(ball: Node, isScale: boolean = true) {
        ball.getComponent(Sprite).enabled = false;
        find('label', ball).active = false;
        find('bingo', ball).active = false;
        // if (Datas_game_903.isEntryJackpot) {
        //     const drip = find('drip', ball);
        //     drip.active = true;
        // } else {
        //     const dripAni = find('dripAni', ball);
        //     dripAni.active = true;
        //     dripAni.getComponent(sp.Skeleton).setAnimation(0, 'animation', false);
        // }
        const dripAni = find('dripAni', ball);
        dripAni.active = true;
        dripAni.getComponent(sp.Skeleton).setAnimation(0, 'animation', false);

        if (isScale) ball.scale = Vec3.ONE;
    }

    private hideDrip(ball: Node) {
        ball.getComponent(Sprite).enabled = true;
        find('label', ball).active = true;
        find('bingo', ball).active = true;
        find('drip', ball).active = false;
        find('dripAni', ball).active = false;
    }
}


