import { _decorator, Component, Node } from 'cc';
import { UserMgr } from '../../module_basic/scripts/UserMgr';
import { SceneDef } from '../../scripts/SceneDef';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { LanguageData } from 'db://assets/module_basic/scripts/language/LanguageData';
const { ccclass, property } = _decorator;

@ccclass('BingoFrogGameHUD')
export class BingoFrogGameHUD extends Component {
    start() {
                if (!UserMgr.inst.uid) {
                    tgx.SceneUtil.loadScene(SceneDef.START);
                    return;
                }
                tgx.UIMgr.inst.closeAll();
        
                // if (RoomMgr.inst.isOnline) {
                //     // tgx.UIMgr.inst.showUI(UIChat, null, null, gameNet);
                // }
            }
        
            async onBtnExitClicked() {
                 //this.node.getParent().getChildByName('Machine').getComponent(AudioSource).stop();
                tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
                    if (ok) {
                        // mouse.node.close();
                        await RoomMgr.inst.exitRoom();
                        //tgx.SceneUtil.loadScene(Slots4GameMgr.inst.entryScene);
                    }
                });
            }
}


