import { instantiate } from 'cc';
import { v3 } from 'cc';
import { Prefab } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { BingoFrogCardGrid } from './BingoFrogCardGrid';
import { Enum } from 'cc';
import { SpriteFrame } from 'cc';
import { BingoFrogData } from './BingoFrogData';
import { Sprite } from 'cc';
import { sp } from 'cc';
import { director } from 'cc';
const { ccclass, property } = _decorator;

export enum CardType {
    SMALL,
    BIG
}
Enum(CardType);

@ccclass('BingoFrogCardManager')
export class BingoFrogCardManager extends Component {
    @property(Prefab) ballPre: Prefab = null;
    @property(Node) content: Node = null;
    @property({ type: CardType, tooltip: '大卡或小卡' }) cardType: CardType = CardType.BIG;
    @property([SpriteFrame]) cardBgFrames: SpriteFrame[] = [];
    @property({ type: Sprite, tooltip: '展示的bingo节点' }) bingoCom: Sprite = null;
    @property({ type: Node, tooltip: 'bingo后的遮罩' }) bingoMask: Node = null;
    @property({ type: sp.Skeleton, tooltip: 'bingo后的文字动画展示' }) bingoTextAni: sp.Skeleton = null;
    @property({ type: sp.Skeleton, tooltip: 'b后的动画展示' }) bingoAni: sp.Skeleton = null;
    @property({ type: Node, tooltip: 'b后的动画展示' }) turboTitle: Node = null;

    private _cardIndex = 0;
    private _isBingo: boolean = false;

    initCard(data, cardIndex) {
        this._cardIndex = cardIndex;
        for (let index = 0; index < 25; index++) {
            const item = instantiate(this.ballPre);
            item.parent = this.content;
            item.getComponent(BingoFrogCardGrid).initGrid(data.items[index], index, this._cardIndex, this);
            const offset_y = index % 5;
            const offset_x = Math.floor(index / 5);
            if (this.cardType === CardType.BIG) {
                item.position = v3(-340 + 170 * offset_x, 285 - 167 * offset_y);
            } else {
                item.position = v3(-170 + 85 * offset_x, 145 - 83 * offset_y);
            }
        }

        director.on(BingoFrogData.EventName.Jackpot, this.entryJackpot, this);
        director.on(BingoFrogData.EventName.bingo, this.bingo, this);
    }

    protected onDisable(): void {
        director.off(BingoFrogData.EventName.Jackpot, this.entryJackpot, this);
        director.off(BingoFrogData.EventName.bingo, this.bingo, this);
    }

    //-----------------测试-检测是否达成bingo, 返回是否bingo及bingo的下标--------------------
    testBingo() {
        const data = BingoFrogData.bingoWayRules;
        let isBingo = false;
        let bingoBallArr = [];
        let missBall = [];
        for (let i = 0; i < data.length; i++) {
            for (let k = 0; k < data[i].length; k++) {
                if (!BingoFrogData.touchLightBalls[this._cardIndex].includes(data[i][k])) {
                    missBall.push(data[i][k]);
                }
            }

            if (missBall.length === 0) {
                bingoBallArr = data[i];
                isBingo = true;
                break;
            }
            missBall = [];
        }

        isBingo && this.currentBingo(bingoBallArr);
        // return { missBallArr: missBallArr, isBingo: isBingo };
    }
    //----------------------------------------------------------------------------------------

    //进入奖池
    private entryJackpot() {
        // if (this._isBingo) {

        // } else {

        // }
        this.bingoMask.active = false;
        this.bingoCom.node.active = false;
        this.turboTitle.active = true;
        this.bingoMask.active = false;
        this.node.getComponent(Sprite).spriteFrame = this.cardBgFrames[1];
    }

    //当前卡牌bingo
    private currentBingo(bingoBallArr) {
        this._isBingo = true;
        this.bingoMask.active = true;
        this.bingoTextAni.node.active = true;
        this.bingoAni.node.active = true;
        this.bingoAni.setAnimation(0, 'animation2', false);
        this.bingoTextAni.setAnimation(0, 'animation', false);
        director.emit(BingoFrogData.EventName.bingo, bingoBallArr, this._cardIndex);

        this.scheduleOnce(() => {
            this.bingoTextAni.node.active = false;
            this.bingoAni.node.active = false;
            this.bingoCom.node.active = true;
        }, 2);
    }

    //其他卡牌bingo处理
    private bingo(bingoArr, cardIndex) {
        if (cardIndex != this._cardIndex) this.bingoMask.active = true;
    }
}


