import { Sprite } from 'cc';
import { director } from 'cc';
import { tween } from 'cc';
import { Tween } from 'cc';
import { Vec3 } from 'cc';
import { Color } from 'cc';
import { SpriteFrame } from 'cc';
import { Label } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { BingoFrogData } from './BingoFrogData';
import { BingoFrogCardManager } from './BingoFrogCardManager';
const { ccclass, property } = _decorator;

enum ballStatus {
    COMMON, //普通状态
    CHECK, //点击后
    BINGO, //中奖
    TURBO, //进入奖池阶段，已点？
    TIP, //发出的球未点击提示
    MISS, //缺少这个就BINGO的
}

const ballColor = [new Color().fromHEX('#ffd28f'), new Color().fromHEX('#fef600'),
new Color().fromHEX('#ffd28f'), new Color().fromHEX("#fef600"), Color.RED, new Color().fromHEX('#530101')]
@ccclass('BingoFrogCardGrid')
export class BingoFrogCardGrid extends Component {
    @property(Label) numLab: Label = null;
    @property(Sprite) ballBg: Sprite = null;
    @property([SpriteFrame]) ballStatusFrames: SpriteFrame[] = [];

    private _ballNumber: number = 0; //当前球的编号
    private _isHave: boolean = false; //是否发过这颗球
    private _ballBlinkTween: Tween<Node> = null;
    private _isCheck: boolean = false; //是否已经有了且已经点击过了
    private _index = -1; //当前数字格子的下标
    private _cardIndex = -1; //当前格子属于那一张卡的下标
    private _isMiss: boolean = false; //是否是缺的那一个
    private _cardMgr: BingoFrogCardManager = null;
    private _isBingo: boolean = false;

    initGrid(data, index, cardIndex, parent: BingoFrogCardManager) {
        this._index = index;
        this._cardIndex = cardIndex;
        this._cardMgr = parent;
        this.numLab.string = data.number + '';
        this._ballNumber = data.number;
        this.node.on(Node.EventType.TOUCH_END, this.onTouchEnd, this);
        director.on(BingoFrogData.EventName.newBall, this.ballListener, this);
        director.on(BingoFrogData.EventName.frogMissBall, this.missBall, this);
        director.on(BingoFrogData.EventName.bingo, this.bingo, this);
    }

    protected onDisable(): void {
        this.offListener();
        director.off(BingoFrogData.EventName.newBall, this.ballListener, this);
        director.off(BingoFrogData.EventName.frogMissBall, this.missBall, this);
    }
    private offListener() {
        this.node.off(Node.EventType.TOUCH_END, this.onTouchEnd, this);
        director.off(BingoFrogData.EventName.bingo, this.bingo, this);
    }

    private bingo(bingoArr, cardIndex) {
        this.offListener();
        this._isBingo = true;
        this._ballBlinkTween && (this._ballBlinkTween.stop(), this._ballBlinkTween = null);
        this.numLab.node.scale = Vec3.ONE;
        if (bingoArr.includes(this._index) && cardIndex === this._cardIndex) {
            this.numLab.color = ballColor[ballStatus.BINGO];
            this.ballBg.spriteFrame = this.ballStatusFrames[ballStatus.BINGO];
        } else {
            if (this._isHave) {
                //有出现过但是未点击的全部设为已点击
                BingoFrogData.touchLightBalls[this._cardIndex].push(this._index);
                this.numLab.color = ballColor[ballStatus.CHECK];
                this.ballBg.spriteFrame = this.ballStatusFrames[ballStatus.CHECK];
            }
        }
    }

    private missBall(msg) {
        if (msg.missBallArr.includes(this._index) && msg.cardIndex === this._cardIndex) {
            if (!this._ballBlinkTween) this.numLab.color = ballColor[ballStatus.MISS];
            this.ballBg.spriteFrame = this.ballStatusFrames[ballStatus.MISS];
            this._isMiss = true;
        }
    }

    private ballListener(data) {
        if (data === this._ballNumber && !this._isHave) {
            this._isHave = true;
            if (this._isBingo) {
                this.check();
            } else {
                this.scheduleOnce(this.ballBlink, 1);
            }
        }
    }

    private ballBlink() {
        // if (this._isBingo) return;
        if (this._isCheck) return;
        this.numLab.color = ballColor[ballStatus.TIP];
        this._ballBlinkTween && (this._ballBlinkTween.stop(), this._ballBlinkTween = null);
        this._ballBlinkTween = tween(this.numLab.node).to(0.4, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.4, { scale: Vec3.ONE })
            .union()
            .repeatForever()
        this._ballBlinkTween.start();
    }

    private onTouchEnd() {
        if (this._isHave) {
            this.check();
        } else {
            tween(this.numLab).to(0.1, { color: Color.BLACK })
                .to(0.1, { color: Color.WHITE })
                .union()
                .repeat(3)
                .call(() => {
                    if (this._isCheck) {
                        this.numLab.color = ballColor[ballStatus.CHECK];
                    } else if (this._isMiss) {
                        this.numLab.color = ballColor[ballStatus.MISS];
                    } else {
                        this.numLab.color = ballColor[ballStatus.COMMON];
                    }
                })
                .start();
        }
    }

    //被点击响应后
    private check() {
        this._ballBlinkTween && (this._ballBlinkTween.stop(), this._ballBlinkTween = null);
        // Tween.stopAllByTarget(this.numLab)
        this.numLab.node.scale = Vec3.ONE;
        this._isCheck = true;
        this.ballBg.spriteFrame = this.ballStatusFrames[ballStatus.CHECK];
        this.numLab.color = ballColor[ballStatus.CHECK];
        this.node.off(Node.EventType.TOUCH_END, this.onTouchEnd, this);
        director.off(BingoFrogData.EventName.newBall, this.ballListener, this);

        BingoFrogData.touchLightBalls[this._cardIndex].push(this._index);
        if (BingoFrogData.touchLightBalls[this._cardIndex].length >= 4) {
            //当前卡片点亮大于等于4的检测是否有却一Bingo的
            const missBallArr = this.isMissingBall();
            if (missBallArr.length > 0) director.emit(BingoFrogData.EventName.frogMissBall, { missBallArr: missBallArr, cardIndex: this._cardIndex });
            if (!this._isBingo) this._cardMgr.testBingo(); //被点击了一个，检测有没有bingo
        }
    }

    //判断是否却少一个bingo，返回缺少那个的下标集合
    private isMissingBall() {
        const data = BingoFrogData.bingoWayRules;
        let missBallArr = [];
        let missBall = [];
        for (let i = 0; i < data.length; i++) {
            if (missBall.length === 1) {
                missBallArr.push(missBall[0]);
            }
            missBall = [];
            for (let k = 0; k < data[i].length; k++) {
                if (!BingoFrogData.touchLightBalls[this._cardIndex].includes(data[i][k])) {
                    missBall.push(data[i][k]);
                }
            }
        }

        return missBallArr;
    }
}


