import { director } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { BingoFrogData } from './BingoFrogData';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { UIOpacity } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('BIngoFrogBuyCardMgr')
export class BIngoFrogBuyCardMgr extends Component {


    private onBuyCard(event, data) {
        //data 购买了几张卡牌（5为特殊玩法，统一是4张）
        const cardNum = Number(data);
        tween(this.node).to(.2, { scale: Vec3.ZERO }).start();
        tween(this.node.getComponent(UIOpacity)).to(.2, { opacity: 0 }).call(() => {
            director.emit(BingoFrogData.EventName.buyCardSuccess, cardNum);
        }).start();
    }
}


